import React from "react";
import wrapper from "../../../commons/WebComponentWrapper";
import Step from "@mui/material/Step";

const dispatchedEvents = [];

const SrsMuiStep = function (props, ref) {
  return (
    <Step
      active={props.active}
      completed={props.completed}
      disabled={props.disabled}
      expanded={props.expanded}
      theme={props.muiTheme}
    >
      <slot />
    </Step>
  );
};

const styles = `
  /* Custom styles for the MuiStep component */
`;

const props = [
  {
    name: "active",
    defaultValue: false,
    type: "boolean",
  },
  {
    name: "completed",
    defaultValue: false,
    type: "boolean",
  },
  {
    name: "disabled",
    defaultValue: false,
    type: "boolean",
  },
  {
    name: "expanded",
    defaultValue: false,
    type: "boolean",
  },
  {
    name: "sx",
    defaultValue: {},
    type: "object"
  }
];

const slots = [
  {
    name: "default",
    allowed: ["srs-step-label"],
  },
];

const WrappedComponent = wrapper(
  React.forwardRef(SrsMuiStep),
  props,
  "mui"
);
export { WrappedComponent, styles, props, dispatchedEvents, slots };
